               @click="openSection('education', 0)"
          ></div>
          <div class="resumeNavBtn experience"
               @click="openSection('experience', 0)"
          ></div>
          <div class="resumeNavBtn skills"
               @click="openSection('skills', 0)"
          ></div>
        </div>
        <img src="../../public/resume.jpg" class="resumeImg">
        <div class="resumeNavBox">
          <div class="navText">
            <h4> Click a highlighted tab to jump to that section.</h4>
            <p>or</p>
            <h4>Download my resume</h4>
            <button type="button"
                    @click="downloadFile()">
              here
            </button>
          </div>
          <div class="mobileNavText">
            <h4>Download my resume</h4>
            <button type="button"
                    @click="downloadFile()">
              here
            </button>
            <p>or</p>
            <h4>Click the tabs below to read more in-depth about each section of my resume.</h4>
          </div>
        </div>
      </div>
      <div class="resumeSectionsContainer">
        <div
            class="resumeSectionWrapper educationWrapper"
            id="education"
        >
          <div class="resumeSectionTextWrapper">
            <div class="resumeSectionHeaderText"
                 @click="openSection('education', 1)"
            >
              <h1 class="resumeSectionHeader">Education</h1>
            </div>
            <div class="resumeSectionParagraphText">
              <h3 class="resumeSectionParagraph">
                I graduated from Evansville North High School in the spring of 2018, and began my collegiate career at Indiana Tech the following fall. I am expected to graduate in May 2022 with a degree in Software Engineering and a minor in Mathematics. Because of my light class load (and a professor who let me watch class recordings instead of attending), I was able to work full-time in the fall of 2021. The same goes for the spring semester for 2022.
              </h3>
            </div>
          </div>
        </div>
        <div class="resumeSectionWrapper skillsWrapper"
             id="skills"
        >
          <div class="resumeSectionTextWrapper">
            <div class="resumeSectionHeaderText"
                 @click="openSection('skills', 1)"
            >
              <h1 class="resumeSectionHeader">Skills</h1>
            </div>
            <div class="resumeSectionParagraphText">
              <h2 class="skillInstructions">Click a skill to read more.</h2>
              <div class="sectionTitles">
                <h2 class="subSectionHeader"
                    id="pythonBtn"
                    @click="openSubSection('python', 'pythonBtn')">
                  Python
                </h2>
                <h2 class="subSectionHeader"
                    id="phpBtn"
                    @click="openSubSection('php','phpBtn')">
                  PHP
                </h2>
                <h2 class="subSectionHeader"
                    id="cBtn"
                    @click="openSubSection('c++','cBtn')">
                  C++
                </h2>
                <h2 class="subSectionHeader"
                    id="jsBtn"
                    @click="openSubSection('javaScript', 'jsBtn')">
                  JavaScript
                </h2>
                <h2 class="subSectionHeader"
                    id="htmlBtn"
                    @click="openSubSection('html', 'htmlBtn')">
                  HTML/CSS/SASS
                </h2>
                <h2 class="subSectionHeader"
                    id="sqlBtn"
                    @click="openSubSection('sql', 'sqlBtn')">
                  SQL/NoSQL
                </h2>
                <h2 class="subSectionHeader"
                    id="vueBtn"
                    @click="openSubSection('vue','vueBtn')">
                  Vue.js
                </h2>
                <h2 class="subSectionHeader"
                    id="juliaBtn"
                    @click="openSubSection('julia','juliaBtn')">
                  Julia
                </h2>
              </div>
              <div
                  class="resumeSubSection"
                  id="javaScript"
                  >
                <h3 class="resumeSectionParagraph">
                  I use Javascript heavily at my current position. Most of our newer projects are done with Vue, but my first project at Ruoff Mortgage (First-Time Homebuyers guide) was scripted entirely through plain Javascript. You can view that site <a href="https://ruoff.com/first-time"><span class ="gotToLink">here</span></a>.
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="html"
              >
                <h3 class="resumeSectionParagraph">
                  HTML/CSS/SASS are the foundations or our web projects at Ruoff.  If our team gets a new project, my role is usually to set up the backend side. However, my summer internship project involved creating a First-Time Homebuyer page from the ground up. I have learned a lot since that first project, so much that I have actually created a ticket to refactor that page. If you would like to view this site, click <a href="https://ruoff.com/first-time"><span class ="gotToLink">here</span></a>.
                </h3>
                <h3 class="resumeSectionParagraph">
                  I am also very familiar when responsive styling. I was tasked with adding responsiveness to a guides page on our site, which you can see <a href="https://www.ruoff.com/guides/home-selling"><span class ="gotToLink">here</span></a>.
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="php"
              >
                <h3 class="resumeSectionParagraph">
                  I first started learning PHP in a couple of junior year project classes in 2020. One was a group project where we created a random movie generator, and another was an individual project where I created a wardrobe generator. The latter would find the average color of an image of clothing, and put an outfit together based on user selection.
                </h3>
                <h3 class="resumeSectionParagraph">
                  When I joined Ruoff, I quickly learned how to use PHP professionally in a MVC environment. Many of my tasks involve pulling in data from another place (mostly from a database) and setting up an endpoint that the frontend can use. Unfortunately, my biggest contributions are on internal sites, so I can’t link them here. I can provide some smaller use cases, however.
                </h3>
                <h3 class="resumeSectionParagraph">
                  These links demonstrate how I set up dynamic routing for a Guides section of our site. There is only one html template, but it renders differently depending on what slug is passed through. Click each one to see the differences.
                  <br><br><a href="https://www.ruoff.com/guides/home-selling"><span class ="gotToLink">Home Selling Guide</span></a><br><br>
                  <a href="https://www.ruoff.com/guides/home-buying"><span class ="gotToLink">Home Buying Guide</span></a><br><br>
                  <a href="https://www.ruoff.com/guides/home-shopping"><span class ="gotToLink">Home Shopping Guide</span></a><br><br>
                  <a href="https://www.ruoff.com/guides/refinance"><span class ="gotToLink">Refinancing Guide</span></a>
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="python"
              >
                <h3 class="resumeSectionParagraph">
                  I first began learning Python during my junior year in 2021 for two project classes. Both were group projects. The first was a “malware” package, where each group member would contribute a different security breaching feature to one project, and they would be combined in the end where a user could select which feature they would like to use. The other was for a stakeholder that my professor brought in. The objective was to have a coin sorting machine that could recognize coins based on images, since sometimes people put rare coins into sorting machines without even knowing it.
                </h3>
                <h3 class="resumeSectionParagraph">
                  For the first project, I decided to create a phishing scam program. The basic process was that a user could add emails of users they wanted to scam (either one by one or using a list in a text file) or they could send out an email batch. The user on the receiving end would get a message from Spotify stating that they needed to verify their account. Clicking the link would lead you to a site that perfectly emulates a Spotify login page, and on submission it would secretly send their email and password to my database. Of course, this was only deployed locally, so my computer was the only device that could access the site. If you would like to see the repository for this project, click <a href="https://github.com/MariaTrimborn/phishingProject"><span class ="gotToLink">here</span></a>.
                </h3>
                <h3 class="resumeSectionParagraph">
                  My second project involved my using Google Vision. First, I created a model for the API to learn from. I gathered 1000+ images of wheat pennies, and 1000+ for normal pennies. I limited the type of coins it could detect for proof of concept. I then wrote a script that would rotate all of those images in a complete circle, saving a copy at every 30 degrees. Google Vision has issues reading text that is not oriented correctly, so I used these rotated images to train another model that would detect if the images were correctly rotated. In theory, a normal coin sorter would not be able to guarantee that the coin is correctly positioned, so this was an important step.
                </h3>
                <h3 class="resumeSectionParagraph">
                  To bring it all together, I would send in an image of a penny, which would then be checked to see if the image was rotated. If it was, then another script would be called that would rotate the image 30 degrees, and another check would be done. This process would continue until the picture was correct. After this, the API would read the text on the coin, and inform the user what year the coin was made. Lastly, it would then detect whether the penny was a modern one, or a wheat penny, using the models I trained as mentioned previously. After this part was finished, the other group members configured my program to run on a raspberry pi that had a camera. This meant that the program could be ran with live camera footage. If you would like to view this project, click <a href="https://github.com/MariaTrimborn/coinSorter"><span class ="gotToLink">here</span></a>.
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="sql"
              >
                <h3 class="resumeSectionParagraph">
                  At my current position, we use MySQL for most of our databases. Most of my tasks related to this include making new tables or columns for specific tasks. One of my most recent tickets has involved setting up our metadata to be generated by the backend. Through this task, I created a table that stores the slugs for each link on our main site, and created another table that houses the description and keywords for each page. The first table is linked to the second through a foreign key.
                </h3>
                <h3 class="resumeSectionParagraph">
                  I was also responsible for creating a database for our Virtual Angel Tree, which is an internal project. Years prior, our HR department would put up a Christmas tree in office that would have gifts listed for a family in need. Employees could then take a tag and bring in the listed gift as a donation. HR decided they wanted to make it virtual, since we have grown considerably and it is difficult to keep track of who is bringing in gifts. Our web team created the site for them, and I was responsible for creating the tables, along with their relationships with one another, in this database.
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="c++"
              >
                <h3 class="resumeSectionParagraph">
                  I have used C++ in multiple classes during my academic career. It is the language that Indiana Tech teaches most of its computer science curriculum through. My most recent projects using it were during my senior year in 2021. If you would like to see my repository for an AVL tree I made with a partner, click
                  <a href="https://github.com/izzykreie/AVLTree"><span class ="gotToLink">here</span></a>.
                </h3>
                <h3 class="resumeSectionParagraph">
                  If you would like to see my repository for creating a hash table, click <a href="https://github.com/MariaTrimborn/hashTable"><span class ="gotToLink">here</span></a>.
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="vue"
              >
                <h3 class="resumeSectionParagraph">
                  The best example I have of my experience with Vue is this current site. I created a base project with the Vue cli and found a script that allows me to deploy it to GitHub pages. You can “vue” this site repository <a href="https://github.com/MariaTrimborn/mariatrimborn.github.io"><span class ="gotToLink">here</span></a>.
                </h3>
                <h3 class="resumeSectionParagraph">
                  Many of our web projects are done through Vue, and we have been slowly adding in more components to our other sites. On a public facing blog page, I was responsible for setting up the logic for which posts would populate and a search bar that allows you to search for specific posts. You can view this site <a href="https://ruoff.com/blog"><span class ="gotToLink">here</span></a>.
                </h3>
              </div>
              <div
                  class="resumeSubSection"
                  id="julia"
              >
                <h3 class="resumeSectionParagraph">
                  I began working with Julia my senior year in 2021. It was for a class called “Organization of Programming Languages”, and the objective was to learn how different types of languages operate. Every two weeks, we would be given an assignment is a different language. However, the main objective of the class was to pick one language that we would use to create a semester project.
                </h3>
                <h3 class="resumeSectionParagraph">
                  For this, I picked Julia, as it seemed similar to Python. My idea was to graph my top Spotify genres and songs. I first got a gigantic JSON file from Spotify that contained my listening history for the past two years. I then parsed out what I needed, sent some HTTP requests to Spotify’s API to get more data, and then graphed the result. If you would like to see this project’s repository, click <a href="https://github.com/MariaTrimborn/JuliaProj"><span class="gotToLink">here</span></a>.
                </h3>
              </div>
