            >
              <h1 class="resumeSectionHeader">Experience</h1>
            </div>
            <div class="resumeSectionParagraphText">
              <h3 class="resumeSectionParagraph">
                My earliest job experience listed in this section is a Conference Services Internship at Indiana Tech. There I was responsible for planning and coordinating events that were at Tech, whether it be for an internal or external client. I would also plan out the days for our AV staff, and direct them on what needed to be done. I also worked closely with clients to get details on what they wanted for their event, and gained knowledge on how to communicate in a professional environment.
              </h3>
              <h3 class="resumeSectionParagraph">
                My current job at Ruoff Mortgage started with an internship in May 2021. I was responsible for working with a large group of interns to create a First-Time Homebuyers landing page. This page would provide all the necessary Information to those looking to buy a home. Since I was the only web intern, all of the site creation responsibility was given to me, with a design layout and content being done by the other interns.
              </h3>
              <h3 class="resumeSectionParagraph" id="experienceInfo">
                I then began working part-time in August as a Junior Web Developer, and moved to full time in October. Our department uses Agile methodologies to plan our projects and bi-weekly sprints. I actively work on new features to our sites, as well as watching Jira for any bug tickets that may come in through the day. My main duties are mostly backend related, such as writing scheduled jobs or creating new endpoints for data on our sites. However, more recently I have been creating the logic in our newer Vue projects, as well as responsive styling. Overall, I have learned a broad array of web development skills, and continue to grow upon them every day.
              </h3>
            </div>
          </div>
