</template>
<script>
export default {
  name: 'Resume',
  props: {
    msg: String
  },
  data() {
    return {
      openTab: "",
      currentSection: "",
      currentBtn: "",
    };
  },

  methods: {
    openSection(id, numCheck){
      let section = document.getElementById(id);
      let experienceSection = document.getElementById("experienceInfo")
      if (section.classList.contains("showSection") && numCheck === 0) {
        if(section.id === "experience"){
          this.scrollToBottom(section);
          setTimeout(()=>{
                experienceSection.scrollIntoView({behavior: "smooth"});
              } , 100
          );
        }else{
          section.scrollIntoView({behavior: "smooth"});
        }
      } else if (section.classList.contains("showSection") && numCheck === 1){
          section.classList.remove("showSection");
      } else{
        section.classList.add("showSection");
        if(section.id === "experience"){
          this.scrollToBottom(section);
          setTimeout(()=>{
                experienceSection.scrollIntoView({behavior: "smooth"});
              } , 100
          );
        }else{
          section.scrollIntoView({behavior: "smooth"});
        }
      }
    },
    async scrollToBottom(section){
      section.classList.add("showSection");
      await this.openSection();
    },
    openSubSection(id, btnId) {
      let subSection = document.getElementById(id);
      let sectionBtn = document.getElementById(btnId);
      if (this.openTab === "") {
        this.openTab = id;
        this.currentBtn = btnId;
        subSection.classList.add("showSubSection");
        sectionBtn.classList.add("changeBtnColor");
      } else {
        let tabToClose = document.getElementById(this.openTab);
        let btnToChange = document.getElementById(this.currentBtn);
        if (this.openTab !== id && tabToClose.classList.contains("showSubSection")) {
          subSection.classList.add("showSubSection");
          sectionBtn.classList.add("changeBtnColor");
          tabToClose.classList.remove("showSubSection");
          btnToChange.classList.remove("changeBtnColor");
          this.openTab = id;
          this.currentBtn = btnId;
        } else {
          subSection.classList.add("showSubSection");
          sectionBtn.classList.add("changeBtnColor");
        }
      }
    },
    downloadFile() {
        var link = document.createElement("a");
        link.download = "mariaTrimbornResume.pdf";
        link.href = "/resume.pdf";
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        //delete link;
    }
  }
}
</script>
