<template>
  <div id="app">
    <navBar/>
    <home-page v-if="homePage"></home-page>
    <about-page v-if="aboutPage"></about-page>
    <resume-page v-if="resumePage"></resume-page>
    <contact-page v-if="contactPage"></contact-page>
  </div>
</template>

<script>
// @ is an alias to /src
import navBar from '@/components/navBar.vue'
import homePage from '@/views/Home.vue'
import aboutPage from '@/views/About.vue'
import resumePage from "./views/Resume.vue";
import contactPage from "./views/Contact.vue";

export default {
name: 'Home',
components: {
  resumePage,
  navBar,
  homePage,
  aboutPage,
  contactPage
},
  computed: {
    homePage() {
      if(this.$route.path === "/" || this.$route.path === "/home" ) {
        return true
      } else {
        return false
      }
    },
    aboutPage() {
      if(this.$route.path === "/about" || this.$route.path === "/about" ) {
        return true
      } else {
        return false
      }
    },
    resumePage() {
      if(this.$route.path === "/resume" || this.$route.path === "/resume" ) {
        return true
      } else {
        return false
      }
    },
    contactPage() {
      if(this.$route.path === "/contact" || this.$route.path === "/contact" ) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>
